<template>
    <div class="login-wrapper">
        <!--  -->
        <div class="login-a"> 
            <van-row>
                <van-col span="4">
                    <span class="left">
                        <van-icon name="arrow-left" @click="pathgo"/>
                    </span>
                </van-col>
                <van-col span="16"></van-col>
                <van-col span="4"></van-col>
            </van-row>
        </div>
        <!--  -->
        <div class="login-b"> 
            <van-cell-group>
                <van-field v-model="username" placeholder="//" />
                <van-field v-model="password" type="password" placeholder="" />
                <span class="iconfont icon-yincang" v-show="!ishiden" @click="ishiden = !ishiden"></span>
                <span class="iconfont icon-xianshi" v-show="ishiden" @click="ishiden= !ishiden"></span>
                <span class="wjmm"></span>
            </van-cell-group>
        </div>
        <!--   -->
        <div class="login-c">
            <van-button round type="info" size="large" @click="getlogin"></van-button>
            <van-button round type="info" size="large" @click="login"></van-button>
        </div>
        <!-- , -->
        <div class="login-d">
            <span></span>
            <span @click="goregister"></span>
        </div>
        <!--  -->
        <div class="login-e ">
            <van-divider  :style="{ color: '#ccc', borderColor: '#ccc'}"></van-divider>
            <van-row>
                <van-col span="12">
                    <!-- <img src="../assets/img/QQ.png" alt=""> -->
                    <img src="../assets/img/qq.jpg" alt="">
                    <p>QQ</p>
                </van-col>
                <van-col span="12">
                    <!-- <img src="../assets/img/wx.png" alt=""> -->
                    <img src="../assets/img/wx.jpg" alt="">
                    <p></p>
                </van-col>
            </van-row>
        </div>
        <p><a href="javascript:;"></a></p>
    </div>
</template>

<script>
import { Notify } from 'vant';
export default {
    data() {
        return {
            username:null,
            password:null,
            ishiden:false,
        }
    },
    methods:{
        pathgo() {
            this.$router.go(-1); //
        },
        // 
        getlogin(){
            window.console.log("");
            // ajaxAPI
            // this.$axios.get('http://192.168.54.65:3000/login', {
            // this.$axios.get('http://192.168.54.22:3000/login', {
            this.$axios.get('/login', {
                params: {
                    username:this.username,
                    password:this.password
                }
            })
            .then((response)=>{
                console.log(response);
                // 1.tokencookielocalStorage
                if(response.data.code ==200){
                    let token = response.data.result.token;
                    console.log("",token);
                    localStorage.login = token;
                    this.$router.push("/my");
                    Notify({ type: 'success', message: '',duration:1000 });
                }else{
                    
                    Notify({  type: 'danger', message: '',duration: 1000 });
                }
            })
        },
        // 
        login(){
            this.$router.push("/my");
        },
        goregister(){
            this.$router.push("/register");
        }
    }
};
</script>

<style lang="scss" scoped>
    .login-wrapper{
        position: relative;
        padding:  0 20px 50px 20px;
        // 
        .login-a {
            width: 100%;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1000;
            color: black;
            background: white;
            text-align: center;
            line-height: 44px;
            font-size: 18px;
            .left {
                display: block;
                text-align: center;
                line-height: 44px;
                font-size: 22px;
            }
        }
        // 
        .login-b{
            position: relative;
            padding: 44px 0 20px 0;
            .van-field{
                margin: 10px 0;
                border-bottom: .01rem solid #efefef;
                position: relative;
            }
            .iconfont{
                position: absolute;
                right: 100px;
                bottom: 10px;
                color: #CCCCCC;
            }
            .wjmm{
                display: block;
                width: 80px;
                position: absolute;
                right: 0;
                bottom: 10px;
            }
            .wjmm::after{
                content: "";
                display: block;
                clear: both;
                width: 1px;
                height: 20px;
                background: #CCCCCC;
                position: absolute;
                top: 2px;
                left: -10px;
                z-index: 999;
            }
        }
        //
        .login-c{
            position: relative;
            .van-button{
                margin-bottom: 10px;
                border: none;
            }
            .van-button:nth-child(1){
                background: #FCB7B3;
            }
            .van-button:nth-child(2){
                background: white;
                color: red;
                border: solid 1px #FCB7B3;
            }
        }
         // , 
        .login-d{
            position: relative;
            font-size: 14px;
            color: #cccccc;
            margin-top: 10px;
            >span:nth-child(1){
                position: absolute;
                left: 0;
            }
            >span:nth-child(2){
                position: absolute;
                right: 0;
            }
        }
         //
        .login-e {
            width: 100%;
            position: relative;
            color: #CCCCCC;
            font-size: 12px;
            .van-divider{
                margin-top: 100px;
                font-size: 12px;
            }
             // qq, 
            .van-col{
                text-align: center;
                img{
                    width: 60px;   
                    height: 60px;
                }
                
            }
        }
        p{
            font-size: 12px;
            color: #CCCCCC;
            text-align: center;
            a{
                font-size: 14px;
                color: blue !important;
            }
        }

    }
</style>