import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Swiper from "../views/Swiper.vue";
import Gg from "../views/Gg.vue";
import Cart from "../views/Cart.vue";
import Category from "../views/Category.vue";
import Login from "../views/Login.vue";
import Detail from "../views/Detail.vue";
import My from "../views/My.vue";
import Confirm from "../views/Confirm.vue";
import Search from "../views/Search.vue";
import Register from "../views/Register.vue";
import Pay from "../views/Pay.vue";
import Order from "../components/My/Order.vue";
import Collection from "../components/My/Collection.vue";
import Address from "../components/My/Address.vue";
import Add from "../components/My/Add.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "",
    component: Swiper
  },
  {
    path: "/gg",
    name: "",
    component: Gg
  },
  {
    path: "/home",
    name: "",
    component: Home
  },
  {
    path: "/category",
    name: "",
    component: Category
  },
  {
    path: "/cart",
    name: "",
    component: Cart
  },
  {
    path: "/login",
    name: "",
    component: Login
  },
  {
    path: "/detail",
    name: "",
    component: Detail
  },
  {
    path: "/my",
    name: "",
    component: My
  },
  {
    path: "/confirm",
    name: "",
    component: Confirm
  },
  {
    path: "/search",
    name: "",
    component: Search
  },
  {
    path: "/register",
    name: "",
    component: Register
  },
  {
    path: "/pay",
    name: "",
    component: Pay
  },
  {
    path: "/order",
    name: "",
    component: Order
  },
  {
    path: "/collection",
    name: "",
    component: Collection
  },
  {
    path: "/address",
    name: "",
    component: Address
  },
  {
    path: "/add",
    name: "",
    component: Add
  }
];

const router = new VueRouter({
  mode: "history",
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
  },
  routes
});

export default router;
